import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of, tap } from 'rxjs';
import { TaskStatus } from '../models/task-status';
import { TaskStatusesCacheService } from '../cache-services/task-statuses-cache.service';

const API_BASE_URL = 'https://localhost:7036';

@Injectable({
  providedIn: 'root',
})
export class TaskStatusesService {
  constructor(private httpClient: HttpClient, private cacheService: TaskStatusesCacheService) {}

  getTaskStatuses(): Observable<TaskStatus[]> {
    const cachedData = this.cacheService.get('cachedData');
    if (cachedData) {
      return of(cachedData);
    } else {
      return this.httpClient.get<TaskStatus[]>(`${API_BASE_URL}/api/taskstatuses`, { responseType: 'json' }).pipe(
        tap((data) => {
          this.cacheService.set('cachedData', data);
        })
      );
    }
  }

  getTaskStatusByTaskStatusID(taskStatusID: number): Observable<TaskStatus> {
    return this.httpClient.get<TaskStatus>(`${API_BASE_URL}/api/taskstatuses/searchbytaskstatusid/${taskStatusID}`, {
      responseType: 'json',
    });
  }

  insertTaskStatus(newTaskStatus: TaskStatus): Observable<TaskStatus> {
    return this.httpClient.post<TaskStatus>(`${API_BASE_URL}/api/taskstatuses`, newTaskStatus, {
      responseType: 'json',
    });
  }

  updateTaskStatus(existingTaskStatus: TaskStatus): Observable<TaskStatus> {
    return this.httpClient.put<TaskStatus>(`${API_BASE_URL}/api/taskstatuses`, existingTaskStatus, {
      responseType: 'json',
    });
  }

  deleteTaskStatus(taskStatusID: number): Observable<string> {
    return this.httpClient.delete<string>(`${API_BASE_URL}/api/taskstatuses?TaskStatusID=${taskStatusID}`);
  }
}
