import { inject } from '@angular/core';
import { CanActivateFn } from '@angular/router';
import { JwtHelperService } from '@auth0/angular-jwt';
import { LoginService } from '../services/login.service';

export const canActivateGuard: CanActivateFn = (route, state) => {
  const token = sessionStorage.getItem('currentUser')
    ? JSON.parse(sessionStorage.getItem('currentUser') as string).token
    : null;

  if (
    inject(LoginService).isAuthenticated() &&
    inject(JwtHelperService).decodeToken(token)['http://schemas.microsoft.com/ws/2008/06/identity/claims/role'] ==
      route.data['role']
  ) {
    return true;
  } else {
    return false;
  }
};
