import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of, tap } from 'rxjs';
import { TaskPriority } from '../models/task-priority';
import { TaskPrioritiesCacheService } from '../cache-services/task-priorities-cache.service';

const API_BASE_URL = 'https://localhost:7036';

@Injectable({
  providedIn: 'root',
})
export class TaskPrioritiesService {
  constructor(private httpClient: HttpClient, private cacheService: TaskPrioritiesCacheService) {}

  getTaskPriorities(): Observable<TaskPriority[]> {
    const cachedData = this.cacheService.get('cachedData');
    if (cachedData) {
      return of(cachedData);
    } else {
      return this.httpClient.get<TaskPriority[]>(`${API_BASE_URL}/api/taskpriorities`, { responseType: 'json' }).pipe(
        tap((data) => {
          this.cacheService.set('cachedData', data);
        })
      );
    }
  }

  getTaskPrioritytByTaskPriorityID(taskPriorityID: number): Observable<TaskPriority> {
    return this.httpClient.get<TaskPriority>(
      `${API_BASE_URL}/api/taskpriorities/searchbytaskpriorityid/${taskPriorityID}`,
      { responseType: 'json' }
    );
  }

  insertTaskPriority(newTaskPriority: TaskPriority): Observable<TaskPriority> {
    return this.httpClient.post<TaskPriority>(`${API_BASE_URL}/api/taskpriorities`, newTaskPriority, {
      responseType: 'json',
    });
  }

  updateTaskPriority(existingTaskPriority: TaskPriority): Observable<TaskPriority> {
    return this.httpClient.put<TaskPriority>(`${API_BASE_URL}/api/taskpriorities`, existingTaskPriority, {
      responseType: 'json',
    });
  }

  deleteTaskPriority(taskPriorityID: number): Observable<string> {
    return this.httpClient.delete<string>(`${API_BASE_URL}/api/taskpriorities?TaskPriorityID=${taskPriorityID}`);
  }
}
